import { Component } from '@angular/core';
import { NavController, NavParams, ToastController } from 'ionic-angular';
import * as WC from 'woocommerce-api';
import { ProductDetailsPage } from "../product-details/product-details";

@Component({
  selector: 'page-product-by-category',
  templateUrl: 'product-by-category.html',
})
export class ProductByCategoryPage {

  WooCommerce: any;
  products: any[];
  page: number;
  category: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public toastCtrl: ToastController) {

    this.page = 1;
    this.category = this.navParams.get("category");

    this.WooCommerce = WC({
      url: "http://localhost:5000/wordpress/",
      consumerKey: "ck_55706aaccfce6cf79cfebab6bca853b5ac9ffb1f",
      consumerSecret: "cs_e052ecd2fca5a243a9eccd16f2ccede0fac501d9"
    });

    this.WooCommerce.getAsync("products?filter[category]=" + this.category.slug).then( (data) => {
      console.log(JSON.parse(data.body));
      this.products = JSON.parse(data.body).products;
    }, (err) => {
      console.log(err)
    })

  }

  LoadMoreProducts(event){
    this.page++;
    console.log("Getting page " + this.page);
    
    this.WooCommerce.getAsync("products?filter[category]=" + this.category.slug + "&page=" + this.page).then(( data ) => {
      let temp = (JSON.parse(data.body).products);

      this.products = this.products.concat(JSON.parse(data.body).products)
      console.log(this.products);
      event.complete();

      if (temp.length < 10) {
        event.enable(false);

        this.toastCtrl.create({
          message: "No more products to load!",
          duration: 3000
        }).present();
      }

    })
  }

  openProductPage(product)
  {
    this.navCtrl.push(ProductDetailsPage, {"product": product} )
  }

}



